.App {
  padding: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  font-size: 14px;
}

.App--Loading {
  color: #fff;
  text-shadow: 0 1px 2px #555;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  margin: auto;
  position: absolute;
  top: -100px;
  left: -100px;
  bottom: 0;
  right: 0;
  background: -webkit-linear-gradient(140deg, rgb(113, 230, 178), rgb(71, 207, 215));
  background: -o-linear-gradient(140deg, rgb(113, 230, 178), rgb(71, 207, 215));
  background: linear-gradient(310deg, rgb(113, 230, 178), rgb(71, 207, 215));
  -webkit-animation: spinny 1s linear infinite;
          animation: spinny 1s linear infinite;
}

@-webkit-keyframes spinny {
  0% {
    -webkit-transform: rotate(0deg) scale(1);
            transform: rotate(0deg) scale(1);
  }
  50% {
    -webkit-transform: rotate(45deg) scale(1.2);
            transform: rotate(45deg) scale(1.2)
  }
  100% {
    -webkit-transform: rotate(360deg) scale(1);
            transform: rotate(360deg) scale(1);
  }
}

@keyframes spinny {
  0% {
    -webkit-transform: rotate(0deg) scale(1);
            transform: rotate(0deg) scale(1);
  }
  50% {
    -webkit-transform: rotate(45deg) scale(1.2);
            transform: rotate(45deg) scale(1.2)
  }
  100% {
    -webkit-transform: rotate(360deg) scale(1);
            transform: rotate(360deg) scale(1);
  }
}



